//========================================================

// make background of block blury kind of like glass
@mixin glass_background{
    // background-color: rgba(255, 255, 255, 0.309); 
    backdrop-filter: blur(30px);
    -webkit-backdrop-filter: blur(100px);
}

@mixin scroll_thumb_horizontal{
    ::-webkit-scrollbar{
        height: 8px;
        background-color: $g_0;
    }
    ::-webkit-scrollbar-thumb{
        background-color: $g_3;
    }  
}
@mixin scroll_thumb_vertical {
    // scrollbar road
    ::-webkit-scrollbar{
        width: 8px;
        background-color: $g_0;
        transition: all 0.6s ease-in-out;
        border-radius: 6px;
    }
    //scrollbar slider
    ::-webkit-scrollbar-thumb{
        background-color: $g_2_1;
        border-radius: 6px;
        transition: all 0.6s ease-in-out;
    }
}
@mixin scroll_thumb_hide{
    // scrollbar road
    ::-webkit-scrollbar{
        visibility: hidden;
    }
    //scrollbar slider
    ::-webkit-scrollbar-thumb{
        visibility: hidden;
    }  
}

// ================================================================

#root, body, html{
    font-family: Fixel;
    margin: 0px;
    padding: 0px;
    scroll-behavior: smooth;
    ::selection{
        // color:  $b_1;
        background-color: $b_3;    
    }
    // overflow-y: hidden;
    overflow-x: hidden;
    @include scroll_thumb_vertical;
}

.App{
    height: 100vh;
}
